exports.push([module.id, ".sidebar-layout{position:relative;background-color:#fcfcfc}@media (min-width:320px){.sidebar-layout{display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;-ms-flex-wrap:nowrap;flex-wrap:nowrap}}@media (min-width:900px){.sidebar-layout{display:-ms-flexbox;display:flex;-ms-flex-direction:row;flex-direction:row;-ms-flex-wrap:nowrap;flex-wrap:nowrap;min-height:calc(100vh - 80px)}}.sidebar-layout__sidebar{margin:1em 0;padding:0 1em;-ms-flex-preferred-size:100%;flex-basis:100%}@media (min-width:900px){.sidebar-layout__sidebar{margin:2em 0;padding:0 2em;border-right:1px solid #1d2870;-ms-flex-preferred-size:33.33%;flex-basis:33.33%}}.sidebar-layout__content{padding:0 1em;margin-bottom:1em;-ms-flex-preferred-size:100%;flex-basis:100%}@media (min-width:900px){.sidebar-layout__content{margin:2em 0;padding:0 2em;-ms-flex-preferred-size:66.66%;flex-basis:66.66%}}.sidebar-layout__header{text-transform:uppercase;font-weight:300}.sidebar-layout__text{max-width:700px}", "", {"version":3,"sources":["/Users/austinreynolds/Documents/MOD/chf-rcomm/packages/client/src/modules/common/components/global/styles/layout/sidebar-layout.scss","/Users/austinreynolds/Documents/MOD/chf-rcomm/packages/client/src/modules/common/components/global/styles/utils/_variables.scss","/Users/austinreynolds/Documents/MOD/chf-rcomm/packages/client/src/modules/common/components/global/styles/utils/_mixins.scss"],"names":[],"mappings":"AAEA,gBAOE,kBAAkB,wBCIL,CD6Bd,yBAxCD,gBEiEM,oBAAA,aAAa,0BF/DK,sBAAA,qBAAQ,gBAAA,CAsC/B,CAAA,yBAxCD,gBEiEM,oBAAA,aAAa,uBF5DE,mBAAA,qBAAQ,iBAAA,6BAKiB,CA8B7C,CA5BC,yBAOE,aAAe,cACC,6BAChB,eAAgB,CACjB,yBAVD,yBAEI,aAAe,cACC,+BAEhB,+BAAA,iBAAkB,CAKrB,CAAA,yBAOC,cAAgB,kBACE,6BAClB,eAAgB,CACjB,yBATD,yBAEI,aAAe,cACC,+BAChB,iBAAkB,CAKrB,CAAA,wBAEC,yBAAyB,eACT,CACjB,sBAEC,eAAgB,CACjB","file":"financing-view.scss","sourcesContent":["@import '../utils/index';\n\n.sidebar-layout {\n  @include xs {\n    @include flex(column, nowrap);\n  }\n  @include md {\n    @include flex(row, nowrap);\n  }\n  position: relative;\n  background-color: $white;\n  @media (min-width: $screenMedium) {\n    min-height: calc(100vh - #{$headerHeight});\n  }\n  &__sidebar {\n    @include md {\n      margin: 2em 0em;\n      padding: 0em 2em;\n      border-right: 1px solid $brandSecondary;\n      flex-basis: 33.33%;\n    }\n    margin: 1em 0em;\n    padding: 0em 1em;\n    flex-basis: 100%;\n  }\n  &__content {\n    @include md {\n      margin: 2em 0em;\n      padding: 0em 2em;\n      flex-basis: 66.66%;\n    }\n    padding: 0em 1em;\n    margin-bottom: 1em;\n    flex-basis: 100%;\n  }\n  &__header{\n    text-transform: uppercase;\n    font-weight: 300;\n  }\n  &__text{\n    max-width: 700px;\n  }\n}\n","// TODO: Implement native css variables\n\n// Brand colors\n$brandPrimary: #FC7B18 !default;\n$brandSecondary: #1D2870 !default;\n// $brandTertiary:\n// $brandQuaternary:\n\n// Colors\n$gray: #E8E8E8 !default;\n$grayDark: darken($gray, 10);\n$grayDarker: darken($gray, 20);\n$grayDarkest: darken($gray, 30);\n$white: #FCFCFC !default;\n\n// Background colors\n$siteBackgroundColor: #F9F9F9 !default;\n$contentBackgroundColor: #F9F9F9 !default;\n$headerBackgroundColor: transparent !default;\n$footerBackgroundColor: #F7F5F5 !default;\n\n// Font Colors\n$linkColor: $brandSecondary;\n\n// Alert Colors\n$infoColor: #DAEDF6 !default;\n$successColor: #DFF0D9 !default;\n$warningColor: #FCF8E4 !default;\n$errorColor: #F1DEDE !default;\n\n// Fonts\n$fontPrimary: 'Roboto', sans-serif;\n$fontSecondary: 'Roboto Condensed', sans-serif;\n\n// Screen Sizes\n// TODO: Decide on final breakpoints\n$screenXSmall: 320px;\n$screenSmall: 600px;\n$screenMedium: 900px;\n$screenLarge: 1200px;\n$screenXLarge: 1440px;\n\n// Element Sizes\n$headerHeight: 80px !default;\n\n// Animation Speeds\n$animationSpeed: 350ms !default;\n$linkAnimationSpeed: calc(#{$animationSpeed} / 2);\n$cardAnimationSpeed: calc(#{$animationSpeed} * 2);\n","// Display mixins\n// Example:\n// @include md {\n//   ...styles\n// }\n// Output:\n// @media (min-width: $screenMedium) {\n//   ...styles\n// }\n@mixin xs {\n  @media (min-width: $screenXSmall) {\n    @content;\n  }\n}\n@mixin sm {\n  @media (min-width: $screenSmall) {\n    @content;\n  }\n}\n@mixin md {\n  @media (min-width: $screenMedium) {\n    @content;\n  }\n}\n@mixin lg {\n  @media (min-width: $screenLarge) {\n    @content;\n  }\n}\n@mixin xl {\n  @media (min-width: $screenXLarge) {\n    @content;\n  }\n}\n@mixin retina {\n  @media only screen and (min-resolution: 192dpi),\n    only screen and (min-resolution: 2dppx) {\n      @content;\n  }\n}\n@mixin print {\n  @media print {\n    @content;\n  }\n}\n\n@mixin lineHeight($font-size) {\n  line-height: calc(#{$font-size} * 1.618);\n}\n\n@mixin absoluteCenter {\n  position: absolute;\n  left: 50%;\n  top: 50%;\n  transform: translate(-50%, -50%);\n}\n\n// flex([row, column])\n// Example:\n// @include flex(row)\n// Creates:\n// display: flex\n// flex-direction: row\n//\n@mixin flex($direction: row, $wrap: nowrap) {\n  @if $direction == \"column\" or $direction == \"row\" {\n    @if $wrap == \"nowrap\" or $wrap == \"wrap\" or $wrap == \"wrap-reverse\" {\n      display: flex;\n      flex-direction: $direction;\n      flex-wrap: $wrap;\n    } @else {\n      @error \"`#{$wrap}` is not a wrapping method. Must be 'nowrap', 'wrap', or 'wrap-reverse'.\";\n    }\n  }\n  @else {\n    @error \"`#{$direction}` is not a valid direction. Must be 'column' or 'row'.\";\n  }\n}\n\n// shadow([bottom, middle, top], [right, center, left], {number in px})\n// Example:\n// @include shadow(bottom, center, 10px)\n// Creates:\n// box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.5)\n//\n@mixin shadow($verticalPosition: middle, $horizontalPosition: center, $blurRadius: 15px) {\n  $yPos: 0px !default;\n  $xPos: 0px !default;\n  @if $verticalPosition == 'bottom' {\n    $yPos: calc(#{$blurRadius} / 4 + 5px);\n  } @else if $verticalPosition == 'middle' {\n    $yPos: 0px;\n  } @else if $verticalPosition == 'top' {\n    $yPos: -#{$blurRadius};\n  } @else {\n    @error \"`#{$verticalPosition}` is not a vertical option. Must be 'bottom', 'middle', or 'top'.\";\n  }\n\n  @if $horizontalPosition == 'right' {\n    $xPos: $blurRadius;\n  } @else if $horizontalPosition == 'center' {\n    $xPos: 0px;\n  } @else if $horizontalPosition == 'left' {\n    $xPos: -#{$blurRadius};\n  } @else {\n    @error \"`#{$horizontalPosition}` is not a horizontal option. Must be 'left', 'right', or 'center'.\";\n  }\n\n  @if is-number($blurRadius) and index('cm' 'mm' 'in' 'px' 'pt' 'pc', unit($blurRadius)) != null {\n  } @else {\n    @error \"`#{$blurRadius}` is not a pixel value. Number must be in `px`\";\n  }\n  box-shadow: $xPos $yPos $blurRadius rgba(0, 0, 0, 0.2);\n}\n\n// animateUnderline({color})\n// Example:\n// @include animateUnderline(#9611ff)\n//\n@mixin animateUnderline($color: $brandSecondary) {\n  @if type-of($color) == 'color' {\n    &:before {\n      position: absolute;\n      background-color: $color;\n      bottom: 0px;\n      content: ' ';\n      height: 2px;\n      left: 0px;\n      transform: scaleX(0);\n      transition: transform $linkAnimationSpeed ease-in-out 0s;\n      visibility: hidden;\n      width: 100%;\n    }\n    &:hover {\n      &:before {\n        transform: scaleX(1);\n        visibility: visible;\n      }\n    }\n  } @else {\n    @error \"`#{$color}` is not a color. Must input a color.\";\n  }\n}\n\n@mixin animateSize($scale: 1.1) {\n  @if type-of($scale) == 'number' {\n    transform: scale(1);\n    &:hover {\n      transform: scale($scale);\n      transition: transform $cardAnimationSpeed ease-in-out;\n    }\n  } @else {\n    @error \"`#{$scale}` is not a number. Must input a number.\"\n  }\n}\n\n@mixin animateBeat($startAngle: -20deg, $endAngle: -25deg) {\n  animation: beat 1s ease infinite alternate;\n  @keyframes beat {\n    from {\n      transform: rotate(#{$startAngle}) scale(1);\n  \t} to {\n      transform: rotate(#{$endAngle}) scale(1.1);\n  \t}\n  }\n\n}\n"],"sourceRoot":""}]);

