,sBACzB,mBAAmB,4BACnB,eAAe,yBACf,4BAA6B,CAEhC,CAAA,8BASC,mBAAqB,CARtB,qCCzEC,kBAAkB,yBAElB,SAAW,YACC,WACD,OACF,4BACT,oBAAoB,kDACpB,0CAAA,iFAAwD,kBACtC,UACP,CDgEZ,2CC5DG,4BAAA,oBAAoB,kBACD,CApHzB,yBD+KE,8BAGI,kBAAqB,CAWxB,CAAA,0BAdD,8BAMI,mBAAqB,CAQxB,CAdA,qCAUG,WAAW,WAC2B,SA1M3B,CA4MZ,oCCtFD,kBAAkB,yBAElB,SAAW,YACC,WACD,OACF,4BACT,oBAAoB,kDACpB,0CAAA,iFAAwD,kBACtC,UACP,CD+EZ,0CC3EG,4BAAA,oBAAoB,kBACD,CD6ErB,yEAEE,kBAAkB,6BACW,aEpNb,CFsNjB,sCAEC,6BAAgC,CAChC,yBAFF,sCAGI,gBAA4B,CAE/B,CAAA,mCAEC,2BAA8B,CAD/B,yCAIG,kBAAkB,YACkB,yBAEpC,kBAAkB,cAElB,8BAA8B,eACO,YAPpB,iBASsB,YAC3B,kBACM,QACV,mCACR,2BAA2B,sBACL,UAdL,CAgBlB,qCASD,mBAAqB,CACtB,yBARD,qCAEI,kBAAqB,CAMxB,CAAA,0BARD,qCAKI,mBAAqB,CAGxB,CAAA,4BAGD,kBAAoB,CACrB,kCAEC,kBAAmB,qBACG,CAFvB,uCAIG,aEpQc,CFqQf","file":"navbar.scss","sourcesContent":["@import './utils/index';\n.admin {\n  &__navbar {\n    $navRoot: &;\n    $lineWidth: 75%;\n    @include flex(column, nowrap);\n    $hamburgerWidth: 40px;\n    $mobileBreakpoint: 1109px;\n    $desktopBreakpoint: 1230px;\n    position: absolute;\n    left: 0px;\n    width: calc(20vw - 2em);\n    padding: 1em 0.5em;\n    // align-items: center;\n    // Added Code Until ScrollMagic Implemented\n    // box-shadow: inset 0px 35px 35px -10px $white;\n    box-shadow: inset 0px #{$headerHeight} 0px 0px $white;\n    justify-content: space-between;\n    top: 0em;\n    transition: box-shadow $animationSpeed ease-in-out;\n    z-index: 100;\n\n    @media (max-width: 400px) {\n      padding: 1em 0em 1em 0.5em;\n    }\n    @media (max-width: 1030px) {\n      align-items: flex-start;\n    }\n    // Commented Out Until ScrollMagic Implemented\n    // &:hover,\n    // &--active {\n    //   box-shadow: inset 0px #{$headerHeight} 0px 0px $white;\n    // }\n    &__header {\n      position: relative;\n      flex-basis: 10%;\n      top: 24px;\n      transform: translateY(-50%);\n      width: auto;\n      img {\n        width: auto;\n        height: calc(80px - 2em);\n      }\n      @media (min-width: 500px) {\n        padding: 0em 1em;\n      }\n      @media (min-width: $mobileBreakpoint) {\n        padding: 0em 2em;\n        flex-basis: 15%;\n        top: auto;\n        transform: none;\n        img {\n          height: calc(80px - 2em);\n          width: 100%;\n        }\n      }\n    }\n    &__logo-link {\n      display: block;\n    }\n    &__content {\n      @include flex(row, nowrap);\n      flex-basis: 90%;\n      justify-content: flex-end;\n      @media (min-width: $mobileBreakpoint) {\n        justify-content: space-between;\n      }\n    }\n    &__hamburger {\n      margin: 1em;\n      cursor: pointer;\n      transform: rotate(0deg) translateY(-40%);\n      transition: .5s ease-in-out;\n      width: $hamburgerWidth;\n\n      @media (max-width: 400px) {\n        width: 30px;\n      }\n      @media (min-width: $mobileBreakpoint) {\n        display: none;\n      }\n    }\n    &__hamburger-item {\n      display: block;\n      position: absolute;\n      background-color: $brandSecondary;\n      border-radius: 3px;\n      height: 3px;\n      left: 0px;\n      opacity: 1;\n      transform: rotate(0deg);\n      transition: .25s ease-in-out;\n      width: 100%;\n\n      &:nth-child(1) {\n        top: 0px;\n      }\n\n      &:nth-child(2),\n      &:nth-child(3) {\n        top: 10px;\n      }\n\n      &:nth-child(4) {\n        top: 20px;\n      }\n\n      &:nth-child(1) {\n        .navbar {\n          &__hamburger {\n            &.open & {\n              top: 10px;\n              width: 0%;\n              left: 50%;\n            }\n          }\n        }\n      }\n\n      &:nth-child(2) {\n        .navbar {\n          &__hamburger {\n            &.open & {\n              transform: rotate(45deg);\n            }\n          }\n        }\n      }\n\n      &:nth-child(3) {\n        .navbar {\n          &__hamburger {\n            &.open & {\n              transform: rotate(-45deg);\n            }\n          }\n        }\n      }\n\n      &:nth-child(4) {\n        .navbar {\n          &__hamburger {\n            &.open & {\n              top: 10px;\n              width: 0%;\n              left: 50%;\n            }\n          }\n        }\n      }\n    }\n    &__collapse {\n      @include flex(column, nowrap);\n      position: absolute;\n      top: 80px;\n      right: 0em;\n      align-items: flex-end;\n      flex-basis: 100%;\n      justify-content: flex-end;\n      max-height: 0px;\n      opacity: 0; // sass-lint:disable-line zero-unit\n      transition: max-height 300ms ease-in-out;\n      visibility: hidden;\n      width: 100%;\n      &.open {\n        background-color: $contentBackgroundColor;\n        max-height: 400px;\n        // margin-bottom: 0.5em;\n        opacity: 1;\n        visibility: visible;\n      }\n      @media (min-width: $mobileBreakpoint) {\n        @include flex(row, nowrap);\n        position: relative;\n        align-items: center;\n        background-color: transparent;\n        flex-basis: 65%;\n        top: unset;\n        right: unset;\n        justify-content: flex-start;\n        max-height: none;\n        opacity: 1;\n        visibility: visible;\n        width: auto;\n      }\n    }\n    &__triggers {\n      display: flex;\n      @media (min-width: $mobileBreakpoint) {\n        @include flex(row, nowrap);\n        align-items: center;\n        flex-basis: 35%;\n        justify-content: space-around;\n      }\n    }\n    &__collapse-item {\n      @include animateUnderline($linkColor);\n      @include md {\n        padding: 0.5em 0.75em;\n      }\n      @include lg {\n        padding: 0.5em 1.25em;\n      }\n      padding: 0.5em 1.25em;\n      &:before {\n        bottom: 5px;\n        left: calc((100% - #{$lineWidth}) / 2);\n        width: $lineWidth;\n      }\n    }\n    &__trigger-item {\n      @include animateUnderline($linkColor);\n      // padding: 10px;\n      &--profile,\n      &--cart {\n        position: relative;\n        background-color: transparent;\n        color: $brandSecondary;\n      }\n      &--profile {\n        padding: 0.5em 17.5px 0.5em 10px;\n        @media (max-width: 400px) {\n          padding: 0.5em 5px 0.5em 5px;\n        }\n      }\n      &--cart {\n        padding: 0.5em 40px 0.5em 10px;\n        &:after {\n          $circleSize: 20px;\n          position: absolute;\n          padding: calc(#{$circleSize} * 0.25);\n          background-color: $brandSecondary;\n          border-radius: 50%;\n          color: $white;\n          content: attr(data-cart-count);\n          font-size: calc(#{$circleSize} * 0.5);\n          height: $circleSize;\n          line-height: calc(#{$circleSize} * 0.5);\n          right: 7.5px;\n          text-align: center;\n          top: 50%;\n          transform: translateY(-50%);\n          vertical-align: middle;\n          width: $circleSize;\n        }\n      }\n      &--search {\n        @include md {\n          padding: 0.5em 0.75em;\n        }\n        @include lg {\n          padding: 0.5em 1.25em;\n        }\n        padding: 0.5em 1.25em;\n      }\n    }\n    &__search-text {\n      padding-right: 0.5em;\n    }\n    &__trigger-item-icon {\n      margin-right: 0.5em;\n      vertical-align: middle;\n      path {\n        color: $brandPrimary;\n      }\n    }\n  }\n\n}\n","// Display mixins\n// Example:\n// @include md {\n//   ...styles\n// }\n// Output:\n// @media (min-width: $screenMedium) {\n//   ...styles\n// }\n@mixin xs {\n  @media (min-width: $screenXSmall) {\n    @content;\n  }\n}\n@mixin sm {\n  @media (min-width: $screenSmall) {\n    @content;\n  }\n}\n@mixin md {\n  @media (min-width: $screenMedium) {\n    @content;\n  }\n}\n@mixin lg {\n  @media (min-width: $screenLarge) {\n    @content;\n  }\n}\n@mixin xl {\n  @media (min-width: $screenXLarge) {\n    @content;\n  }\n}\n@mixin retina {\n  @media only screen and (min-resolution: 192dpi),\n    only screen and (min-resolution: 2dppx) {\n      @content;\n  }\n}\n@mixin print {\n  @media print {\n    @content;\n  }\n}\n\n@mixin lineHeight($font-size) {\n  line-height: calc(#{$font-size} * 1.618);\n}\n\n@mixin absoluteCenter {\n  position: absolute;\n  left: 50%;\n  top: 50%;\n  transform: translate(-50%, -50%);\n}\n\n// flex([row, column])\n// Example:\n// @include flex(row)\n// Creates:\n// display: flex\n// flex-direction: row\n//\n@mixin flex($direction: row, $wrap: nowrap) {\n  @if $direction == \"column\" or $direction == \"row\" {\n    @if $wrap == \"nowrap\" or $wrap == \"wrap\" or $wrap == \"wrap-reverse\" {\n      display: flex;\n      flex-direction: $direction;\n      flex-wrap: $wrap;\n    } @else {\n      @error \"`#{$wrap}` is not a wrapping method. Must be 'nowrap', 'wrap', or 'wrap-reverse'.\";\n    }\n  }\n  @else {\n    @error \"`#{$direction}` is not a valid direction. Must be 'column' or 'row'.\";\n  }\n}\n\n// shadow([bottom, middle, top], [right, center, left], {number in px})\n// Example:\n// @include shadow(bottom, center, 10px)\n// Creates:\n// box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.5)\n//\n@mixin shadow($verticalPosition: middle, $horizontalPosition: center, $blurRadius: 15px) {\n  $yPos: 0px !default;\n  $xPos: 0px !default;\n  @if $verticalPosition == 'bottom' {\n    $yPos: calc(#{$blurRadius} / 4 + 5px);\n  } @else if $verticalPosition == 'middle' {\n    $yPos: 0px;\n  } @else if $verticalPosition == 'top' {\n    $yPos: -#{$blurRadius};\n  } @else {\n    @error \"`#{$verticalPosition}` is not a vertical option. Must be 'bottom', 'middle', or 'top'.\";\n  }\n\n  @if $horizontalPosition == 'right' {\n    $xPos: $blurRadius;\n  } @else if $horizontalPosition == 'center' {\n    $xPos: 0px;\n  } @else if $horizontalPosition == 'left' {\n    $xPos: -#{$blurRadius};\n  } @else {\n    @error \"`#{$horizontalPosition}` is not a horizontal option. Must be 'left', 'right', or 'center'.\";\n  }\n\n  @if is-number($blurRadius) and index('cm' 'mm' 'in' 'px' 'pt' 'pc', unit($blurRadius)) != null {\n  } @else {\n    @error \"`#{$blurRadius}` is not a pixel value. Number must be in `px`\";\n  }\n  box-shadow: $xPos $yPos $blurRadius rgba(0, 0, 0, 0.2);\n}\n\n// animateUnderline({color})\n// Example:\n// @include animateUnderline(#9611ff)\n//\n@mixin animateUnderline($color: $brandSecondary) {\n  @if type-of($color) == 'color' {\n    &:before {\n      position: absolute;\n      background-color: $color;\n      bottom: 0px;\n      content: ' ';\n      height: 2px;\n      left: 0px;\n      transform: scaleX(0);\n      transition: transform $linkAnimationSpeed ease-in-out 0s;\n      visibility: hidden;\n      width: 100%;\n    }\n    &:hover {\n      &:before {\n        transform: scaleX(1);\n        visibility: visible;\n      }\n    }\n  } @else {\n    @error \"`#{$color}` is not a color. Must input a color.\";\n  }\n}\n\n@mixin animateSize($scale: 1.1) {\n  @if type-of($scale) == 'number' {\n    transform: scale(1);\n    &:hover {\n      transform: scale($scale);\n      transition: transform $cardAnimationSpeed ease-in-out;\n    }\n  } @else {\n    @error \"`#{$scale}` is not a number. Must input a number.\"\n  }\n}\n\n@mixin animateBeat($startAngle: -20deg, $endAngle: -25deg) {\n  animation: beat 1s ease infinite alternate;\n  @keyframes beat {\n    from {\n      transform: rotate(#{$startAngle}) scale(1);\n  \t} to {\n      transform: rotate(#{$endAngle}) scale(1.1);\n  \t}\n  }\n\n}\n","// TODO: Implement native css variables\n\n// Brand colors\n$brandPrimary: #FC7B18 !default;\n$brandSecondary: #1D2870 !default;\n// $brandTertiary:\n// $brandQuaternary:\n\n// Colors\n$gray: #E8E8E8 !default;\n$grayDark: darken($gray, 10);\n$grayDarker: darken($gray, 20);\n$grayDarkest: darken($gray, 30);\n$white: #FCFCFC !default;\n\n// Background colors\n$siteBackgroundColor: #F9F9F9 !default;\n$contentBackgroundColor: #F9F9F9 !default;\n$headerBackgroundColor: transparent !default;\n$footerBackgroundColor: #F7F5F5 !default;\n\n// Font Colors\n$linkColor: $brandSecondary;\n\n// Alert Colors\n$infoColor: #DAEDF6 !default;\n$successColor: #DFF0D9 !default;\n$warningColor: #FCF8E4 !default;\n$errorColor: #F1DEDE !default;\n\n// Fonts\n$fontPrimary: 'Roboto', sans-serif;\n$fontSecondary: 'Roboto Condensed', sans-serif;\n\n// Screen Sizes\n// TODO: Decide on final breakpoints\n$screenXSmall: 320px;\n$screenSmall: 600px;\n$screenMedium: 900px;\n$screenLarge: 1200px;\n$screenXLarge: 1440px;\n\n// Element Sizes\n$headerHeight: 80px !default;\n\n// Animation Speeds\n$animationSpeed: 350ms !default;\n$linkAnimationSpeed: calc(#{$animationSpeed} / 2);\n$cardAnimationSpeed: calc(#{$animationSpeed} * 2);\n"],"sourceRoot":""}]);

