ty: 0; // sass-lint:disable-line zero-unit\n  overflow-x: hidden;\n  overflow-y: hidden;\n  width: 100vw;\n  visibility: hidden;\n  @media (min-width: $screenMedium) {\n    height: calc(100vh - #{$headerHeight});\n  }\n  &--open {\n    opacity: 1;\n    transition: opacity $animationSpeed ease-in-out;\n    visibility: visible;\n    z-index: 10000;\n    top: 77px;\n    left: 0px;\n    .cart {\n      right: 0px;\n      transition: right $animationSpeed ease-in-out;\n    }\n    @media (min-width: 901px) {\n      top: unset;\n      left: unset;\n    }\n  }\n}\n\n.cart {\n  position: absolute;\n  background-color: $contentBackgroundColor;\n  height: 100%;\n  max-width: 100vw;\n  right: -300px;\n  top: 0em;\n  width: 320px;\n  max-height: 640px;\n  overflow-y: scroll;\n  @media (min-width: $screenSmall) {\n    max-height: unset;\n  }\n  &__dismiss {\n    display: flex;\n    width: 100%;\n    background-color: $brandSecondary;\n    justify-content: flex-end;\n    align-items: center;\n    height: 50px;\n    color: white;\n    box-shadow: 0px 6px 14px 0px rgba(0,0,0,.3);\n    svg {\n      margin-left: .5em;\n    }\n  }\n  &__item-list {\n    padding: 0em 1em 3em 1em;\n    max-height: 80%;\n    overflow-y: auto;\n  }\n  &__item {\n    @include flex(row, nowrap);\n    padding: 1em 0em 1em 1em;\n    // padding-left: 2em;\n    align-items: center;\n    border-bottom: 1px solid $brandSecondary;\n    justify-content: space-between;\n  }\n  &__item-title {\n    display: flex;\n    width: 100%;\n    flex-direction: column;\n  }\n  &__item-buttons {\n    button {\n      background-color: inherit\n    }\n    @include flex(row, nowrap);\n    align-items: center;\n  }\n  &__item-name {\n    font-weight: 500;\n    font-size: 1.1em;\n  }\n  &__quantity-container {\n    padding-right: .5em;\n    font-size: .9em;\n  }\n  &__item-quantity {}\n  &__item-updates {\n    button {\n      background-color: inherit\n    }\n    @include flex(column, nowrap);\n    max-width: 30px;\n  }\n  &__item-update {\n    &--add {}\n    &--subtract {}\n  }\n  &__item-remove {}\n  &__summary {\n    @include flex(column, wrap);\n    position: absolute;\n    padding: 1em 2em;\n    background-color: $contentBackgroundColor;\n    bottom: 0px;\n    box-shadow: 0px -5px 15px 0px rgba(0, 0, 0, 0.3);\n    justify-content: space-between;\n    left: 0px;\n    width: 100%;\n  }\n  &__price-container {\n    justify-content: space-between;\n    display: flex;\n    align-items: center;\n    padding-bottom: .75em;\n  }\n  &__total{\n    h1 {\n      line-height: 1em;\n      margin: 0;\n    }\n  }\n  &__price{\n    h2 {\n      line-height: 1em;\n      font-size: 1.1em;\n      margin: 0;\n    }\n  }\n  &__item-none{\n    display: block;\n    padding-top: 1em;\n  }\n  &__checkout {}\n}\n","// Display mixins\n// Example:\n// @include md {\n//   ...styles\n// }\n// Output:\n// @media (min-width: $screenMedium) {\n//   ...styles\n// }\n@mixin xs {\n  @media (min-width: $screenXSmall) {\n    @content;\n  }\n}\n@mixin sm {\n  @media (min-width: $screenSmall) {\n    @content;\n  }\n}\n@mixin md {\n  @media (min-width: $screenMedium) {\n    @content;\n  }\n}\n@mixin lg {\n  @media (min-width: $screenLarge) {\n    @content;\n  }\n}\n@mixin xl {\n  @media (min-width: $screenXLarge) {\n    @content;\n  }\n}\n@mixin retina {\n  @media only screen and (min-resolution: 192dpi),\n    only screen and (min-resolution: 2dppx) {\n      @content;\n  }\n}\n@mixin print {\n  @media print {\n    @content;\n  }\n}\n\n@mixin lineHeight($font-size) {\n  line-height: calc(#{$font-size} * 1.618);\n}\n\n@mixin absoluteCenter {\n  position: absolute;\n  left: 50%;\n  top: 50%;\n  transform: translate(-50%, -50%);\n}\n\n// flex([row, column])\n// Example:\n// @include flex(row)\n// Creates:\n// display: flex\n// flex-direction: row\n//\n@mixin flex($direction: row, $wrap: nowrap) {\n  @if $direction == \"column\" or $direction == \"row\" {\n    @if $wrap == \"nowrap\" or $wrap == \"wrap\" or $wrap == \"wrap-reverse\" {\n      display: flex;\n      flex-direction: $direction;\n      flex-wrap: $wrap;\n    } @else {\n      @error \"`#{$wrap}` is not a wrapping method. Must be 'nowrap', 'wrap', or 'wrap-reverse'.\";\n    }\n  }\n  @else {\n    @error \"`#{$direction}` is not a valid direction. Must be 'column' or 'row'.\";\n  }\n}\n\n// shadow([bottom, middle, top], [right, center, left], {number in px})\n// Example:\n// @include shadow(bottom, center, 10px)\n// Creates:\n// box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.5)\n//\n@mixin shadow($verticalPosition: middle, $horizontalPosition: center, $blurRadius: 15px) {\n  $yPos: 0px !default;\n  $xPos: 0px !default;\n  @if $verticalPosition == 'bottom' {\n    $yPos: calc(#{$blurRadius} / 4 + 5px);\n  } @else if $verticalPosition == 'middle' {\n    $yPos: 0px;\n  } @else if $verticalPosition == 'top' {\n    $yPos: -#{$blurRadius};\n  } @else {\n    @error \"`#{$verticalPosition}` is not a vertical option. Must be 'bottom', 'middle', or 'top'.\";\n  }\n\n  @if $horizontalPosition == 'right' {\n    $xPos: $blurRadius;\n  } @else if $horizontalPosition == 'center' {\n    $xPos: 0px;\n  } @else if $horizontalPosition == 'left' {\n    $xPos: -#{$blurRadius};\n  } @else {\n    @error \"`#{$horizontalPosition}` is not a horizontal option. Must be 'left', 'right', or 'center'.\";\n  }\n\n  @if is-number($blurRadius) and index('cm' 'mm' 'in' 'px' 'pt' 'pc', unit($blurRadius)) != null {\n  } @else {\n    @error \"`#{$blurRadius}` is not a pixel value. Number must be in `px`\";\n  }\n  box-shadow: $xPos $yPos $blurRadius rgba(0, 0, 0, 0.2);\n}\n\n// animateUnderline({color})\n// Example:\n// @include animateUnderline(#9611ff)\n//\n@mixin animateUnderline($color: $brandSecondary) {\n  @if type-of($color) == 'color' {\n    &:before {\n      position: absolute;\n      background-color: $color;\n      bottom: 0px;\n      content: ' ';\n      height: 2px;\n      left: 0px;\n      transform: scaleX(0);\n      transition: transform $linkAnimationSpeed ease-in-out 0s;\n      visibility: hidden;\n      width: 100%;\n    }\n    &:hover {\n      &:before {\n        transform: scaleX(1);\n        visibility: visible;\n      }\n    }\n  } @else {\n    @error \"`#{$color}` is not a color. Must input a color.\";\n  }\n}\n\n@mixin animateSize($scale: 1.1) {\n  @if type-of($scale) == 'number' {\n    transform: scale(1);\n    &:hover {\n      transform: scale($scale);\n      transition: transform $cardAnimationSpeed ease-in-out;\n    }\n  } @else {\n    @error \"`#{$scale}` is not a number. Must input a number.\"\n  }\n}\n\n@mixin animateBeat($startAngle: -20deg, $endAngle: -25deg) {\n  animation: beat 1s ease infinite alternate;\n  @keyframes beat {\n    from {\n      transform: rotate(#{$startAngle}) scale(1);\n  \t} to {\n      transform: rotate(#{$endAngle}) scale(1.1);\n  \t}\n  }\n\n}\n"],"sourceRoot":""}]);

