.21em;\n  padding: 20px;\n  text-align: center;\n  background: #E5E5E5;\n  box-shadow: 6.92px 4.6px #C3C3C5;\n  &__header {\n    font-size: $baseHeaderSize;\n    display: inline-block;\n    position: relative;\n  }\n  &__header-super {\n    font-size: .6em;\n    vertical-align: text-top;\n    display: inline-block;\n    margin-top: .19em;\n  }\n  &__header-sub {\n    font-size: .33em;\n    font-weight: 300;\n    position: absolute;\n    right: -.5em;\n    top: 2.8em;\n  }\n  &__text-container {\n    display: flex;\n    flex-direction: column;\n    justify-content: space-between;\n    height: 12.9em;\n    width: 14.9em;\n    padding: 1.25em;\n    margin: 3.25em auto 0;\n    background: #f7f7f7;\n  }\n  &__sub-header {\n    font-size: 1.9em;\n    padding-bottom: .43em;\n    margin-bottom: .43em;\n    position: relative;\n    line-height: 1.2em;\n    &:before {\n     @extend %center-with-absolute;\n     @extend %baseline;\n    }\n  }\n  &__text {\n    @extend %text-shared;\n    position: relative;\n    bottom: 10px;\n  }\n  &__sub-text {\n    font-size: 1.13em;\n    font-weight: 300;\n  }\n  &__button {\n    background: #fc7b18;\n    border-radius: 6px;\n    color: #fff;\n    @extend %workouts-btn;\n    grid-area: button;\n  }\n}\n",".promo-split-bg {\n  display: grid;\n  grid-template-areas: 'text' 'photo';\n  grid-template-rows: auto minxmax(auto 283px);\n  grid-template-columns: 100%;\n  font-size: .6em;\n  height: auto;\n  overflow: hidden;\n  @media (min-width: 620px) {\n    font-size: .5em;\n    grid-template-areas: 'text photo';\n    grid-template-columns: 40% 60%;\n    max-height: 39.3em;\n  }\n  @media (min-width: 768px) {\n    font-size: .6em;\n  }\n  @media (min-width: 1050px) {\n    font-size: .8em;\n  }\n  @media (min-width: 1200px) {\n    font-size: 1em;\n  }\n  &__text-container {\n    background: #000000;\n    padding: 4.5em 2.9em;\n  }\n  &__header {\n    font-size: $baseHeaderSize;\n    margin-bottom: 1em;\n    letter-spacing: normal;\n    font-weight